import React from 'react';
import Toolbar from '../components/Toolbar/Toolbar';
import './Services.css'
import Card from '../components/Cards/Card';
import FloatingActionButtons from '../components/Buttons/FloatingButton';
import SpringModal from '../components/Modal/Modal';
export default function Services() {
    const [open, setopen] = React.useState(false);

    return (
        <div >
            <Toolbar />
            <div className="c-container">
                <div className="card-container">
                    <div className="cards">
                        <Card image="https://www.namesakeproductions.com/blog/wp-content/uploads/2016/12/Namesake-Corporate-film-production-at-its-best-compressor-1024x485.jpg"
                            title="Production" content="Some quick example text to build on the card title and make up the bulk of the card's content." />
                        <Card image="https://www.nyfa.edu/student-resources/wp-content/uploads/2016/12/Taurus-Final-Reel-1-Avid-MC-Timeline.jpg"
                            title="Editing" content="Some quick example text to build on the card title and make up the bulk of the card's content." />

                        <Card image="data:image/jpeg;base64,"
                            title="Direction" content="Some quick example text to build on the card title and make up the bulk of the card's content." />
                    </div>
                    <div className="cards-left">
                        <Card image="http://pbblogassets.s3.amazonaws.com/uploads/2015/11/Cinematography-Manual-865x505.jpg"
                            title="Cinematographi" content="Some quick example text to build on the card title and make up the bulk of the card's content." />


                    </div>
                </div>
            </div>
            <FloatingActionButtons openModal={() => { setopen(true); console.log(open) }} />
            <SpringModal openValue={open} openModal={() => { setopen(false); console.log(open) }} />
        </div>
    );
}